<template>
	<div class="van-tabbar-page box">
		<div class="header" v-lazy:background="require('../../assets/change_logo.png')">
			<div>
				<img v-lazy="require('../../assets/eth.png')">
				<span>{{ ethBalance }} ETH</span>
			</div>
			<div>
				<img v-lazy="require('../../assets/sfc.png')">
