				<span>{{ isRound ? $t('message.prosperousChange') : $t('message.creationChange') }}</span>
			</div>
			<span>{{ total }} SFC</span>
		</div>
		<div class="content" v-if="!isRound">
			<div>
				<span>1:{{ prop }}</span>
				<span>{{ width }}</span>
				<div :style="{ width: width }"></div>
			</div>
			<div class="change_round" v-for="(item, index) of roundList" :key="index">
				<span>1:{{ item.prop }}</span>
			</div>
		</div>
		<div class="content" v-else>
		    <div>
		    	<span>1:{{ prop }}</span>
		    	<span>{{ width }}</span>
		    	<div :style="{ width: width }"></div>
		    </div>
		</div>
		<div class="input">
			<div class="title">
				<div>
					<i></i>
					<span>{{ $t('message.remaining') }}: {{ lave }} SFC</span>
				</div>
				<router-link to="/changeRecord">
					<img v-lazy="require('../../assets/change_record.png')">
					<span>{{ $t('message.record') }}</span>
				</router-link>
			</div>
			<div class="input_content">
				<div>
					<input type="number" placeholder="0" v-model="eth">
					<span>ETH</span>
				</div>
				<img v-lazy="require('../../assets/change_icon.png')">
				<div>
					<input type="number" readonly :value="eth * 10">
					<span>SFC</span>
				</div>
			</div>
		</div>
		<div class="button">
			<Button @click="changeToken" color="linear-gradient(#814ABC, #4C44D8)">{{ $t('message.define') }}</Button>
			<div @click="detail = true">
			    <img v-lazy="require('../../assets/change_detail.png')" />
			    <span>{{ $t('message.learnMore') }}</span>
			</div>
		</div>
		<self-tabbar active="change"></self-tabbar>
		<overlay :show="detail" @click="detail = false">
		    <div v-if="$i18n.locale === 'zh-CN'" class="detail" @click.stop>
		        <span>SFC</span>
		        <div>
		            SFC
		            <span>Smurf CoinSmurf +SFCSmurfSmurf CoinSFCSFC109Smurf1SFCSmurf1000</span>
		            <p>1.5%SFC</p>
		            <p>2.0x0000000000000000000000000000000000000000</p>
		            
		            <p>11000SFC</p>
		            <p>21:10000ETHSFC100SFC</p>
		            <p>1:8000ETHSFC200SFC</p>
		            <p>1:6000ETHSFC300SFC</p>
		            <p>1:4000ETHSFC400SFC</p>
		            <p>3</p>
		            
		            <p>19000SFC1:4000ETHSFC,</p>
		            <p>220SFC1%</p>
		            <p>31:4000ETH1000SFC</p>
		            
		            <span>SFCETH</span>
		            <p>120%V1V2V3V425%</p>
		            <p>230%ETH2050 ETH20100ETHV2</p>
		            <p>330%</p>
		            <p>420%</p>
		        </div>
		        <Button @click="detail = false" color="linear-gradient(#814ABC, #4C44D8)">{{ $t('message.define') }}</Button>
		    </div>
		    <div v-else class="detail" @click.stop>
		        <span>SFC exchange allocation scheme</span>
		        <div>
		            1Understanding SFC
		            <span>Smurf coin is the token before the main network of Smurf blockchain system goes online. Through the original "issuance mechanism" + "game burning" mechanism, SFC's deflationary rise and appreciation are realized, so as to build the value consensus of Smurf (blue wizard) open financial ecosystem.Smurf coin (SFC) is a new generation of high-performance privacy public chain. All transactions are anonymous. It supports smart contracts and can issue anonymous coins and assets.The total amount of SFC tokens is 1 billion, of which 900 million are locked in the smart contract address, which is only used for compensation of Smurf game ecosystem. Among them, 100 million are circulated through the creation and prosperity exchange mechanism of SFC, and then burned through the just needed use of Smurf game system until the last 10 million, then used in a circular way.</span>
		            <p>Participation: consumption of 5% equivalent SFC</p>
		            <p>Eater Address:0x0000000000000000000000000000000000000000</p>
		            2Genesis issue
		            <p>Total amount: 10 million SFC, issued in four phases;</p>
		            <p>The first exchange ratio is 1:10000 (ETH: SFC), with a total amount of 1 million SFC,</p>
		            <p>The second exchange ratio is 1:8000 (ETH: SFC), with a total amount of 2 million SFC,</p>
		            <p>The third exchange ratio is 1:6000 (ETH: SFC), with a total amount of 3 million SFC,</p>
		            <p>In the fourth phase, the exchange ratio is 1:4000 (ETH: SFC), with a total amount of 4 million SFC;</p>
		            <p>After the Genesis issue, it will enter the Prosperity exchange.</p>
		            3Prosperity exchange
		            <p>Total amount: 90 million SFC, the initial exchange ratio is 1:4000 (ETH: SFC), and the exchange is completed;</p>
		            <p>For every 200000 SFC, the proportion of exchange price will be increased by 1%;</p>
		            <p>The base price of the first price increase is the ETH counter price of Chuangshi 1:4000 stage. For every 10 million SFC, the base price increase index becomes the price base of the last stage;</p>
		            4Exchange allocation
		            <span>All the ETH obtained from SFC exchange will enter the Smurf contract address and be allocated to global evangelist, alliance partners, dividend balance and R & D team in proportion as follows:</span>
		            <p>20% of the exchange proceeds are weighted to all preachers around the world,V1, V2, V3 and V4 are equally divided into 25%,Weighted dividend once a week;</p>
		            <p>30% weighted to global alliance partners,ETH purchase is required for alliance partner qualification,The top 20 only need 50 ETH,100 ETH after 20,The alliance partner defaults to V2 level,Dividend once a week;</p>
		            <p>30% is automatically calculated by the built-in algorithm to balance the daily dividend of members;</p>
		            <p>The last 20% belongs to the R & D team.</p>
		        </div>
		        <Button @click="detail = false" color="linear-gradient(#814ABC, #4C44D8)">{{ $t('message.define') }}</Button>
		    </div>
		</overlay>
	</div>
</template>

<script>
	import selfTabbar from '@/components/self-tabbar'
	import { Toast, Overlay, Button } from "vant"
	import { isWallet  } from '../../static/initData'

	export default {
		name: "change",
		components: { selfTabbar, Toast, Overlay, Button },
		data() {
			return {
				isRound: false,
				ethBalance: "0.00",
				sfcBalance: "0.00",
				total: "0.00",
				prop: 1,
				width: "56.5%",
				lave: "0.00",
				eth: "",
				detail: false,
				roundList: []
			}
		},
		methods: {
			init() {
				if (this.$cache.change) {
				    this.ethBalance = this.$cache.change.ethBalance
				    this.sfcBalance = this.$cache.change.sfcBalance
					this.isRound = this.$cache.change.isRound
				    this.total = this.$cache.change.total
				    this.prop = this.$cache.change.prop
				    this.width = this.$cache.change.width
				    this.lave = this.$cache.change.lave
				    this.roundList = this.$cache.change.roundList
				} else {
				    this.$cache.change = {}
				}
				
				this.getData()
			},
			getData() {
				this.$contract.tokenChanger.methods._genesisRoundSuccess().call((err, res) => {
				    if (!err) {
				        if (res) {
				            this.$contract.tokenChanger.methods.CurrentHeyDayChagneProp().call((err, res) => {
				                if (!err) {
				                    this.total = this.$fromWei(res.total, 2)
				                    this.prop = this.$fromWei(res.prop)
				                    let changed = this.$fromWei(res.changed)
				                    this.lave = this.total - changed
				                    this.width = `${((this.lave / this.total) * 100).toFixed(2)}%`
				
				                    this.$cache.change.total = this.total
				                    this.$cache.change.prop = this.prop
				                    this.$cache.change.lave = this.lave
				                    this.$cache.change.width = this.width
				                }
				            })
				        } else {
				            this.$contract.tokenChanger.methods.CurrentRound().call(async (err, res) => {
				                if (!err) {
				                    this.total = this.$fromWei(res.total, 2)
				                    this.prop = this.$fromWei(res.prop)
				                    let changed = this.$fromWei(res.changed)
				                    this.lave = this.total - changed
				                    this.width = `${((this.lave / this.total) * 100).toFixed(2)}%`
				
				                    this.$cache.change.total = this.total
				                    this.$cache.change.prop = this.prop
				                    this.$cache.change.lave = this.lave
				                    this.$cache.change.width = this.width
				
				                    let roundList = []
				                    for (let i = Number(res.roundID); i < 4; i++) {
				                        let round = await this.$contract.tokenChanger.methods.ChangeRoundAt(i).call()
				                        roundList.push({
				                            amount: this.$fromWei(round.total) / 10000,
				                            prop: this.$fromWei(round.prop)
				                        })
				                    }
				
				                    this.roundList = roundList
				                    this.$cache.change.roundList = roundList
				                }
				            })
				        }
				
				        this.isRound = res
						this.$cache.change.isRound = res
				    }
				})
				
				this.$web3.eth.getBalance(this.$address).then(balance => {
				    this.ethBalance = this.$fromWei(balance, 2)
				    this.$cache.change.ethBalance = this.ethBalance
				})
				
				this.$contract.erc20token.methods.balanceOf(this.$address).call((err, res) => {
				    if (!err) {
				        this.sfcBalance = this.$fromWei(res, 2)
				        this.$cache.change.sfcBalance = this.sfcBalance
				    }
				})
			},
			changeToken() {
			    if (this.eth === 0 || this.eth === "") {
			        return Toast.fail(this.$t('message.changeNull'))
			    } else if ((this.eth * 1000) % 10 !== 0) {
			        return Toast.fail(this.$t('message.changeErr1'))
			    } else if (this.eth * this.prop > this.lave) {
			        return Toast.fail(this.$t('message.changeErr2'))
			    }
			
			    this.$contract.tokenChanger.methods.DoChangeToken().send({
			        from: this.$address,
			        value: this.$toWei(`${this.eth}`)
			    }).on("transactionHash", txHash => {
			        console.log(txHash)
			    }).on("error", console.error)
			    .on("receipt", res => {
			        this.eth = 0
			        this.getData()
			    })
			},
			onCopySuccess() {
			    Toast.success(this.$t('message.copySuccess'))
			},
			onCopyError() {
			    Toast.fail(this.$t('message.copyFail'))
			}
		},
		mounted() {
			if (isWallet) {
			    this.init()
			}
		}
	}
</script>

